.hero {
  width: 100%;
  min-height: 100vh;
}
.hero__bg {
  position: absolute;
  top: 0;
  left: calc(50% - 40vw);
  width: 80vw;
  height: 50vh;
  background: linear-gradient(45deg,#7be7d7 1%,#7be7d7 40%,#7be7d7 40%,#86a4e3 100%);
}

.hero__title {
  position: relative;
  width: 100%;
  height: 100vh;
}

.hero__lookbook {
  position: absolute;
  width: 807px;
  height: 400px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -60%);
}

.hero__lookbook-desktop {
  top: -60px;
}

.hero__lookbook-mobile {
  display: none;
  width: 100%;
  position: absolute;
  top: 80px;
  left: 0;
  p {
    font-family: $sans;
    font-style: italic;
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: 11.25rem;
    line-height: 0.8;
    font-weight: 500;
  }
}

.hero__subtitle {
  font-family: $sans;
  font-size: 5.6rem;
  color: #70e4d0;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translateX(-50%);
}

@media (max-width: 980px) {

  .hero__lookbook {
    width: 100%;
  }

  .hero__lookbook-desktop {
    display: none;
  }
  .hero__lookbook-mobile {
    display: block;
  }

  .hero__subtitle {
    display: none;
  }
}
