@mixin color($color) {
  .card__name, .card__size_title, .card__button {
    color: $color;
  }

  .card__button:hover {
    background: $color;
    color: white;
  }
}

.card {
  &__bastien { @include color( #FF414A ); }
  &__leo { @include color( #6A5952 ); }
  &__maxime { @include color( #D79D49 ); }
  &__rachel { @include color( #EFA9B3 ); }
  &__victoria { @include color( #2B8F71 ); }
  &__celeste { @include color( #5FA5CE ); }
}
