.logo {
  z-index: 99999;
  position: fixed;
  top: 40px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;

  &__img {
    width: 45px;
    height: 45px;
    margin-bottom: 10px;

    img {
      width: 100%;
    }
  }

  &__text {
    font-family: $serif;
    margin: 0;
    color: #575756;
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 6px;
  }
}
