@font-face {
  font-family: 'DidotItalic';
  src: url("../fonts/DidotItal.eot") format("embedded-opentype"), url("../fonts/DidotItal.ttf") format("truetype");
  font-weight: normal;
  font-style: italic;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html, body {
  width: 100%;
  font-size: 16px;
  font-size: 1rem;
  color: #1e1a1a;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

@media (max-width: 600px) {
  html, body {
    font-size: 10px;
  }
}

@media (max-width: 458px) {
  html, body {
    font-size: 8px;
  }
}

@media (max-width: 365px) {
  html, body {
    font-size: 6px;
  }
}

.no_animation {
  display: none;
}

.button {
  padding: 0 60px;
  border: 1px solid;
  border-radius: 50px;
  text-decoration: none;
  height: 42px;
  line-height: 48px;
  font-size: 18px;
}

.logo {
  z-index: 99999;
  position: fixed;
  top: 40px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo__img {
  width: 45px;
  height: 45px;
  margin-bottom: 10px;
}

.logo__img img {
  width: 100%;
}

.logo__text {
  font-family: "Lato", sans-serif;
  margin: 0;
  color: #575756;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 6px;
}

.hero {
  width: 100%;
  min-height: 100vh;
}

.hero__bg {
  position: absolute;
  top: 0;
  left: calc(50% - 40vw);
  width: 80vw;
  height: 50vh;
  background: linear-gradient(45deg, #7be7d7 1%, #7be7d7 40%, #7be7d7 40%, #86a4e3 100%);
}

.hero__title {
  position: relative;
  width: 100%;
  height: 100vh;
}

.hero__lookbook {
  position: absolute;
  width: 807px;
  height: 400px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -60%);
}

.hero__lookbook-desktop {
  top: -60px;
}

.hero__lookbook-mobile {
  display: none;
  width: 100%;
  position: absolute;
  top: 80px;
  left: 0;
}

.hero__lookbook-mobile p {
  font-family: DidotItalic, serif;
  font-style: italic;
  margin: 0;
  padding: 0;
  text-align: center;
  font-size: 11.25rem;
  line-height: 0.8;
  font-weight: 500;
}

.hero__subtitle {
  font-family: DidotItalic, serif;
  font-size: 5.6rem;
  color: #70e4d0;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translateX(-50%);
}

@media (max-width: 980px) {
  .hero__lookbook {
    width: 100%;
  }
  .hero__lookbook-desktop {
    display: none;
  }
  .hero__lookbook-mobile {
    display: block;
  }
  .hero__subtitle {
    display: none;
  }
}

.card {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin: 150px 0;
}

.card__describe, .card__images {
  width: 50%;
  position: relative;
  height: 100vh;
}

.card__describe_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.card__head, .card__name, .card__size_title, .card__size_item, .card__button {
  font-family: DidotItalic, serif;
}

.card__head {
  font-size: 26px;
  margin-bottom: 15px;
}

.card__name {
  font-size: 70px;
  margin-bottom: 25px;
}

.card__description {
  font-size: 14px;
  max-width: 514px;
  margin-bottom: 25px;
  line-height: 1.8;
}

.card__size_title {
  font-size: 18px;
  margin-bottom: 24px;
}

.card__size_list {
  width: 375px;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  margin-bottom: 24px;
}

.card__size_item {
  font-size: 14px;
}

.card__button {
  transition: 0.5s color, 0.5s background;
}

.card__images_gradient {
  position: absolute;
  top: 10%;
  right: 0;
  width: 96%;
  height: 75vh;
}

.card__images_foot {
  position: absolute;
  right: 0;
  bottom: 0;
}

.card-revers {
  flex-direction: row-reverse;
}

.card-revers .card__images_gradient {
  left: 0;
}

.card-revers .card__images_foot {
  left: 0;
}

.card__bastien .card__name, .card__bastien .card__size_title, .card__bastien .card__button {
  color: #FF414A;
}

.card__bastien .card__button:hover {
  background: #FF414A;
  color: white;
}

.card__leo .card__name, .card__leo .card__size_title, .card__leo .card__button {
  color: #6A5952;
}

.card__leo .card__button:hover {
  background: #6A5952;
  color: white;
}

.card__maxime .card__name, .card__maxime .card__size_title, .card__maxime .card__button {
  color: #D79D49;
}

.card__maxime .card__button:hover {
  background: #D79D49;
  color: white;
}

.card__rachel .card__name, .card__rachel .card__size_title, .card__rachel .card__button {
  color: #EFA9B3;
}

.card__rachel .card__button:hover {
  background: #EFA9B3;
  color: white;
}

.card__victoria .card__name, .card__victoria .card__size_title, .card__victoria .card__button {
  color: #2B8F71;
}

.card__victoria .card__button:hover {
  background: #2B8F71;
  color: white;
}

.card__celeste .card__name, .card__celeste .card__size_title, .card__celeste .card__button {
  color: #5FA5CE;
}

.card__celeste .card__button:hover {
  background: #5FA5CE;
  color: white;
}
