.card {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin: 150px 0;

  &__describe, &__images {
    width: 50%;
    position: relative;
    height: 100vh;
  }

  &__describe_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__head, &__name, &__size_title, &__size_item, &__button {
    font-family: $sans;
  }

  &__head {
    font-size: 26px;
    margin-bottom: 15px;
  }

  &__name {
    font-size: 70px;
    margin-bottom: 25px;
  }

  &__description {
    font-size: 14px;
    max-width: 514px;
    margin-bottom: 25px;
    line-height: 1.8;
  }

  &__size_title {
    font-size: 18px;
    margin-bottom: 24px;
  }

  &__size_list {
    width: 375px;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__size_item {
    font-size: 14px;
  }

  &__button {
    transition: 0.5s color, 0.5s background;
  }

  &__images_gradient {
    position: absolute;
    top: 10%;
    right: 0;
    width: 96%;
    height: 75vh;
  }

  &__images_foot {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  &-revers {
    flex-direction: row-reverse;

    .card__images_gradient {
      left: 0;
    }

    .card__images_foot {
      left: 0;
    }
  }
}

@import "./card_color";
